:root {
    --dark-bg: #252627;
    --checked-clr: #7494ea;
  }
  .light-bg {
    --light-bg: #edf7f6;
    background: var(--light-bg);
  }

.container {
    // display: flex;
  }
  
  input[type="checkbox"] {
    cursor: pointer;
    -webkit-appearance: none;
    appaerance: none;
    position: relative;
    width: 57px;
    height: 27px;
    border: 2px solid #ccdbdc;
    outline: none;
    border-radius: 30px;
    transition: 300ms;
  }
  input[type="checkbox"]::before {
    content: "";
    position: absolute;
    top: 1.5px;
    left: 2px;
    width: 20px;
    height: 20px;
    background: #cccccc;
    border-radius: 50%;
    transition: background 300ms ease, transform 300ms ease;
  }
  input[type="checkbox"]:checked::before {
    transform: translateX(30px);
    background: var(--checked-clr);
  }
  input[type="checkbox"]:checked {
    border-color: var(--checked-clr);
  }
  

// -------
.navbar{
    padding: 1em 10em;
    display: flex;
    justify-content: space-between;
    align-items:center;
    background-color: transparent;
    @include responsive(m){
        display: none;
    }
    &_darkmode{
        background-color: transparent;
    }
    &_menu{
        color:black;
        display: flex;
        list-style: none;
        justify-content: space-around;
        background-color: transparent;
        width:500px;
        align-items:center;
        transition:width .1s;
    }
    &_list{
        text-decoration: none;
        opacity: 0;
        height:44px;
        transition: opacity .3s ease .3s;
        &.active{
            opacity:.8;
            transition: opacity .3s ease .3s;
        } 
    }
    &_toggle{
        width: 25px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;
        transition: transform 330ms ease-out;
        overflow:hidden;
        padding:7px;
        border: 5px solid rgba(16, 16, 16, 0.728);
        border-radius: 50%;
        &.active{
            transform: rotate(-45deg);
        }
        .line-menu {
            background-color:rgba(30, 30, 30, 0.728);
            border-radius: 6px;
            width: 100%;
            height: 5px;
            &.half{
                width:50%;
            }
            &.start{
                transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
                transform-origin: right;
            }
            &.startfinal{
                transform: rotate(-90deg) translateX(3px);
            }
            &.end{
                align-self: flex-end;
                transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
                transform-origin: left;
            }
            &.endfinal{
                transform: rotate(-90deg) translateX(-3px);
            }
          }
    }
}


